.tile {
	position: absolute;
	width: calc(25% - var(--pixel-size));
	height: calc(25% - var(--pixel-size));
	margin: var(--pixel-size);
	border-radius: var(--pixel-size);
	background: var(--tile-background);
	color: var(--primary-text-color);
	font-size: calc(var(--pixel-size) * 4);
	line-height: calc(var(--pixel-size) * 8);
	font-weight: bold;
	transition-property: left, top, transform;
	transition-duration: 200ms, 200ms, 100ms;
	display: flex;
	align-items: center;
	justify-content: center;
}

/* -=-=-=- START OF TEXT COLOR -=-=-=- */
.tile8,
.tile16,
.tile32,
.tile64,
.tile128,
.tile256,
.tile512,
.tile1024,
.tile2048,
.tile4096,
.tile8192 {
	color: var(--secondary-text-color);
}
/* -=-=-=-= END OF TEXT COLOR =-=-=-=- */

/* -=-=-=- START OF TEXT SIZE -=-=-=- */
.tile128,
.tile256,
.tile512 {
	font-size: calc(var(--pixel-size) * 3.25);
}

.tile1024,
.tile2048,
.tile4096,
.tile8192 {
	font-size: calc(var(--pixel-size) * 2.75);
}


